import styled from "@emotion/styled";
import { Link, useNavigate } from "react-router-dom";
import { useState, useEffect } from "react";

import { Container } from "@/components/common/layouts/Container";
import { useAuth } from "@/provider/Auth";
import { getDynamicPath, RouterPath } from "@/routes/path";

export const Header = () => {
  const navigate = useNavigate();
  const authInfo = useAuth();

  const [selectedApi, setSelectedApi] = useState(" API ");

  useEffect(() => {
    const storedApi = sessionStorage.getItem("selectedApi");
    if (storedApi) {
      setSelectedApi(storedApi);
    } else {
      sessionStorage.setItem("selectedApi", " API ");
    }
  }, []);

  const handleLogin = () => {
    navigate(getDynamicPath.login());
  };

  const handleApiChange = (event: any) => {
    const apiValue = event.target.value;
    setSelectedApi(apiValue);
    sessionStorage.setItem("selectedApi", apiValue);

    if (apiValue !== " API ") {
      connectToApi(apiValue);
    }
  };

  const connectToApi = (api: any) => {
    let url;
    switch (api) {
      case "":
        url = "https://pnuece.pnu.app/admin/products";
        break;
      case "":
        url = "http://3.37.113.212:8080/manager/products";
        break;
      case "":
        url = "https://muna76.tistory.com/116";
        break;
      case "":
        url = "https://kakao-gift-api.shop/admin";
        break;
      default:
        url = null;
    }
    if (url) {
      window.location.href = url;
    }
  };

  return (
    <Wrapper>
      <Container
        flexDirection="row"
        alignItems="center"
        justifyContent="space-between"
      >
        <Link to={RouterPath.home}>
          <Logo
            src="https://gift-s.kakaocdn.net/dn/gift/images/m640/pc_gift_logo.png"
            alt="  "
          />
        </Link>
        <RightWrapper>
          <Select onChange={handleApiChange} value={selectedApi}>
            <option value=" API " disabled>
               API 
            </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </Select>
          {authInfo ? (
            <>
              <LinkButton onClick={() => navigate(RouterPath.admin)}>
                
              </LinkButton>
              <LinkButton onClick={() => navigate(RouterPath.myAccount)}>
                 
              </LinkButton>
            </>
          ) : (
            <LinkButton onClick={handleLogin}></LinkButton>
          )}
        </RightWrapper>
      </Container>
    </Wrapper>
  );
};

export const HEADER_HEIGHT = "54px";

export const Wrapper = styled.header`
  position: fixed;
  z-index: 9999;
  width: 100%;
  max-width: 100vw;
  height: ${HEADER_HEIGHT};
  background-color: #fff;
  padding: 0 16px;
`;

const Logo = styled.img`
  height: ${HEADER_HEIGHT};
`;
const RightWrapper = styled.div`
  display: flex;
  flex-direction: row;
`;

const Select = styled.select`
  margin-right: 16px;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
`;

const LinkButton = styled.p`
  align-items: center;
  font-size: 14px;
  color: #000;
  text-decoration: none;
  cursor: pointer;
`;
